/* eslint-disable jsx-a11y/anchor-is-valid */
/* eslint-disable jsx-a11y/no-static-element-interactions */
/* eslint-disable jsx-a11y/click-events-have-key-events */
import React from 'react';
import cn from 'classnames';
import Input from 'components/Input';
import styles from './ChangePasswordPage.module.css';

const ChangePasswordPage = ({
  className,
  oldPass,
  newPass,
  newPass2,

  setOldPass,
  setNewPass,
  setNewPass2,

  setValidForm,
  ...props
}) => {
  const oldPassError = !oldPass.length
    ? 'Please enter your current password.'
    : null;

  const newPassError = (np => {
    if (np.length < 3) return ' ';
    if (np.length < 6)
      return 'Password should have at least 6 letters and numbers.';
    if (!/[0-9]/g.test(np)) return 'Password should contain numbers.';

    return null;
  })(newPass);

  const newPass2Error = ((np2, np, nperr) => {
    if (nperr) return ' ';
    if (!nperr && np !== np2) return 'Passwords do not match.';
    return null;
  })(newPass2, newPass, newPassError);

  setValidForm(!oldPassError && !newPassError && !newPass2Error);

  return (
    <div className={cn(className)} {...props}>
      <Input
        className={styles.oldPassInput}
        type="password"
        name="oldPass"
        label="Current password"
        value={oldPass}
        onChange={setOldPass}
      />
      <a
        className={styles.forgotPassLink}
        // eslint-disable-next-line no-alert
        onClick={() => window.alert('Will go somewhere')}
      >
        Forgot your password?
      </a>
      <Input
        type="password"
        name="newPass"
        label="New password"
        value={newPass}
        valid={!newPassError}
        error={newPassError}
        onChange={setNewPass}
      />
      <Input
        type="password"
        name="newPass2"
        label="Confirm new password"
        value={newPass2}
        valid={!newPass2Error}
        error={newPass2Error}
        onChange={setNewPass2}
      />
    </div>
  );
};

export default ChangePasswordPage;
