import React from 'react';
import Page from 'components/Page';
import Faq from 'components/Faq';
import FaqEntry from 'components/Faq/FaqEntry';

import * as faqs from 'data/faq';

const FaqPage = ({ history, creator }) => (
  <Page
    className="lowLevelPage"
    title="FAQs"
    noGap
    onBackNavigation={() => history.goBack()}
  >
    <Faq>
      {creator &&
        faqs.creator.map(item => <FaqEntry item={item} key={item.question} />)}

      {faqs.consumer.map(item => (
        <FaqEntry item={item} key={item.question} />
      ))}
    </Faq>
  </Page>
);

export default FaqPage;
