import React from 'react';
import cn from 'classnames';
import styles from './Footer.module.css';

const Footer = ({ children, noGap, noTopGap, noBottomGap, ...props }) => (
  <footer
    className={cn(styles.footer, {
      [styles.noGap]: noGap,
      [styles.noTopGap]: noTopGap,
      [styles.noBottomGap]: noBottomGap
    })}
    {...props}
  >
    {children}
  </footer>
);

export default Footer;
